import React, { Component } from "react";
import styles from "./akiEfacil.scss";
import akiEfacil from "../../stores/images";
console.log(akiEfacil);
console.log("akiEfacil", styles);


class AkiEFacil extends Component {
  render() {
    return <section className={styles.akiEfacil}>
        <div className={styles.wrapper}>
          <div className={styles.outer_images}>
            {akiEfacil.akiEfacil.map((item, index) => {
              return <div className={styles.img_div}>
                    <img src={item} alt={item} />
                  </div>
            })}
          </div>
          <div className={styles.text}>
            <span>&#64;akiefacil</span>
          </div>
        </div>
      </section>;
  }
}

export default AkiEFacil;



