import React, { Component } from "react";
import styles from "./outrosMiniSites.scss";
import outrosMiniSites from "../../stores/images";
console.log(outrosMiniSites);
console.log("outrosMiniSites", styles);

const content = {
  texts: [
    `Pintar a casa no Interior`,
    `Iluminar o Jardim`,
    `Proteger e tratar as madeiras no exterior`
  ],
  urls: [
    `https://www.youtube.com/watch?v=D5eXAFK57do`,
    `https://www.youtube.com/watch?v=pkfYlt0Bojw&feature=youtu.be`,
    `https://youtu.be/P-3YVgy0yh0`
  ]
};
    


class OutrosMiniSites extends Component {
  render() {
    return <section className={styles.outrosMiniSites}>
        <div className={styles.wrapper}>
          <div className={styles.outer_images}>
            {outrosMiniSites.outrosMiniSites.map((item, index) => {
              return <div className={styles.align_text}>
                  <div className={styles.texts}>
                    <p>Veja também</p>
                    <a target="__blank" href={content["urls"][index]}>
                      {content["texts"][index]}
                    </a>
                  </div>
                  <figure>
                    <img src={item} alt={item} />
                  </figure>
                </div>;
            })}
          </div>
        </div>
      </section>;
  }
}

export default OutrosMiniSites;



