import React, { Component } from "react";
import styles from "./services.scss";
import images from "../../stores/images";

const servicesRows = {
    row1:[
    "Entregas ao domicilio",
    "Click e Recolha",
    "Duplicação de chaves",
    "Montagem de máquinas",
     "Instalações ao domicilio",
    "Cartão Aki Mais"
    ],
    row2:[
        "Afinar Cores",
        "Corte de Madeira",
        "Entregas no Autonovel",
        "Financiamento",
        "Pickup",
        "Loja Online"
    ]
};

class Services extends Component {
  render() {
    return <section className={styles.services}>
        <div className={styles.wrapper}>
          <p className={styles.title}>Serviços que descomplicam</p>
          <div className={styles.top_buttons}>
            <div className={styles.button}>
              <img src={images.icons[0]} alt="" />
              <p>
                Loja online - Todos os artigos estão disponíveis para compra
                online
              </p>
            </div>
            <div className={styles.button} >
              <img src={images.icons[1]} alt="" />
              <p>
                Click &amp; recolha - Pode comprar em aki.pt e recolher no
                AKI
              </p>
            </div>
          </div>
          {Object.keys(servicesRows).map((row, index) => {
            return <div className={styles.row}>
                {servicesRows[row].map((item, index) => {
                  if (row === "row2") index = index + 6;
                  return <div className={styles.services_images}>
                      <img className={styles.img_services} src={images.services[index]} alt={item} />
                    </div>;
                })}
              </div>;
          })}
        </div>
      </section>;
  }
}

export default Services;



