import React, { Component } from "react";
import styles from "./pintarExterior.scss";
import images from "../../stores/images";
console.log(images);
console.log("pintarExterior", styles);


const content = {
  pintarFerro: {
    title: `Nunca foi tão rápido preparar e pintar ferro no exterior`,
    text: `Em menos de 1 minuto, veja como aplicar corretamente e em segurança tinta anti ferrugem e primário para ferro.`,
    urls: [``,`https://youtu.be/Z2F_0dG_4Lc`]
  },
  pintarMadeiras: {
    title: `Conselhos para proteger e pintar madeiras e metais exteriores`,
    text: `Leia este guia e descubra como proteger e preparar superfícies, de madeira ou de metal, antes de passar a tinta para madeira, ou tinta anti ferrugem.`,
    urls: [`https://www.aki.pt/Saiba-como-proteger-e-pintar-madeiras-e-metais-exteriores.aspx`]
  },
  pintarParedes: {
    title: `Pintar paredes exteriores deixou de ser uma dor de cabeça`,
    text: `Neste pequeno vídeo pode ver os passos necessários para
    que a aplicação de tinta de exterior corra da melhor forma. No final, estão indicados os materiais usados, tais como o rolo para pintar, a fita adesiva para proteger, entre outros.`,
    urls: [`https://www.aki.pt/Saiba-como-pintar-paredes-exteriores.aspx`,`https://youtu.be/GyzPA-5jL0I`]
  },

  moveisExteriores: {
    title: `Móveis exteriores sempre impecáveis`,
    text: `Saiba como cuidar e restaurar os seus móveis de exterior com a ajuda de óleo para móveis de madeira, protetor para abrigos, cera para madeira, esmalte anti ferrugem,
    e muitos mais.`,
    urls: [`https://www.aki.pt/Saiba-como-cuidar-dos-moveis-de-exterior.aspx`]
  }
};



const arrContentKeys = Object.keys(content);
  console.log("loloololool ",content["pintarFerro"]);
class PintarExterior extends Component {

switchImagesSides(index,item){
    return <div style={index === 0 ? { justifyContent: "flex-start" } : null} className={index === 1 || index === 3 ? styles.column2 : styles.column1}>
        <figure>
          {index === 0 || index === 2 ? <a target="__blank" href={content[index === 0 ? "pintarFerro" : "pintarParedes"].urls[1]}>
              <div className={styles.triangle} />
            </a> : null}
          <img src={item} alt="" />
        </figure>
      </div>;
}



content(images){
    return images.pintarExterior.map((item, index) => {
        console.log("bricoFicha ", content[arrContentKeys[index]].urls[0]);
              return <div style={index !== 1 && index !== 3 ? { flexWrap: "wrap" } : { flexWrap: "wrap-reverse" }} className={styles.outer}>
                  {index === 0 || index === 2 ? this.switchImagesSides(index, item) : null}
                  <div style={index === 0 ? { justifyContent: "flex-start" } : null} className={index === 0 ? styles.column2 : styles.column1}>
                    <div className={styles.content}>
                      <h4>
                        {content[arrContentKeys[index]].title}
                      </h4>
                      <p>{content[arrContentKeys[index]].text}</p>
                      <div className={styles.links}>
                        {index !== 0 ? <div>
                            <a target="__blank" href={content[arrContentKeys[index]].urls[0]}>
                              Ver bricoficha&rarr;
                            </a>
                          </div> : null}
                        {index !== 1 && index !== 3 ? <div>
                            <a target="__blank" href={content[arrContentKeys[index]].urls[1]}>
                              Ver Video&rarr;
                            </a>
                          </div> : null}
                      </div>
                    </div>
                  </div>
                  {index === 1 || index === 3 ? this.switchImagesSides(index, item) : null}
                </div>;
            })
}

  render() {
    return (
      <section className={styles.pintarExterior}>
        <div className={styles.wrapper}>
          <div className={styles.outer_images}>
            {this.content(images)}
          </div>
        </div>
      </section>
    );
  }
}

export default PintarExterior;



