import React, { Component } from "react";
import styles from "./podeVer.scss";
import podeVer from "../../stores/images";
console.log(podeVer);
console.log("podeVer", styles);

const podeVerRow = {
  title: [
    "Tinta para exterior",
    "Pinceís,rolos e acessórios",
    "Primário para parede de exterior",
    "Tinta para piscinas",
    "Escadas,escadotes e tamboretes"
  ],
  urls: [
    `https://www.aki.pt/tintas-e-drogaria/tintas-exterior.aspx`,
    `https://www.aki.pt/tintas-e-drogaria/pinceis-rolos-e-acessorios.aspx`,
    `https://www.aki.pt/tintas-e-drogaria/tintas-exterior/manutencao-e-preparacao-fachadas/primario-paredes-exteriores.aspx`,
    `https://www.aki.pt/tintas-e-drogaria/tintas-exterior/tinta-superficies-especficas/tinta-para-piscinas.aspx`,
    `https://www.aki.pt/madeiras-e-construcao/escadas-e-tamboretes.aspx`
  ]
};

class PodeVer extends Component {
  render() {
    return <section className={styles.podeVer}>
        <div className={styles.row}>
          <div className={styles.align_images}>
            <div className={styles.inner_images}>
              <p>Pode ver também...</p>
            </div>
            {podeVerRow["title"].map((item, index) => {
              return <div className={styles.outer_images}>
                  <div className={styles.inner_images}>
                    <figure>
                      <img src={podeVer.podeVer[index]} alt={item} />
                    </figure>
                  </div>
                  <div>
                    <a target="__blank" href={podeVerRow["urls"][index]}>
                      {item}&rarr;
                    </a>
                  </div>
                </div>;
            })}
          </div>
        </div>
      </section>;
  }
}

export default PodeVer;



