import React, { Component } from 'react';
import styles from './App.scss';
import Header from '../pages/header';
import Services from '../pages/services';
import PodeVer from "../pages/podeVer";
import AkiEFacil from "../pages/akiEfacil";
import OutrosMiniSites from "../pages/outrosMiniSites";
import PintarExterior from "../pages/pintarExterior";
import Produtos from "../pages/produtos";

console.log("estilos",styles);

class App extends Component {
  render() {
    return <div className={styles.app}>
        <Header />
        <PintarExterior />
        <PodeVer />
        <Produtos/>
        <AkiEFacil />
        <OutrosMiniSites />
        <Services />
      </div>;
  }
}

export default App;



