import React, {Component} from 'react';
import topExterior from '../../assets/images/Topo_Pintar-no-exterior.jpg'
import styles from './header.scss';




class Header extends Component {

    render() {
        return <React.Fragment>
            <header>
              {  console.log("Header ", this.props.images)}
              <div className={styles.image_container} r>
                <img src={topExterior} alt="Topo_Pintar-no-exterior" />
              </div>
              <div className={styles.green_footer} />
              <div className={styles.text_container}>
                <div className={styles.row}>
                  <div className={styles.column1}>
                    <div className={styles.title_text}>
                      <p>
                        Pintar lá fora <br /> sem complicações
                      </p>
                      <h1>
                        Pintar<br /> no exterior
                      </h1>
                    </div>
                  </div>
                  <div className={styles.column2}>
                    <p>
                      Para que o trabalho saia bem à primeira, leia as
                      nossas sugestões sobre o uso de tinta de exterior
                      e muito mais.
                    </p>
                  </div>
                </div>
              </div>
            </header>
          </React.Fragment>;
    }
}

export default Header;


